"use client";

import { useEffect, useState } from "react";
import { Card, CardHeader, CardTitle, CardContent, CardFooter } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Package, ChevronRight, Clock, Loader2 } from "lucide-react";
import { Separator } from "@/components/ui/separator";
import Link from "next/link";

export default function OrdersPage() {
    const [orders, setOrders] = useState([]);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        async function fetchOrders() {
            try {
                const res = await fetch("/api/user/orders");
                if (res.ok) {
                    const data = await res.json();
                    setOrders(data);
                }
            } catch (error) {
                console.error("Fetch Orders Error:", error);
            } finally {
                setIsLoading(false);
            }
        }
        fetchOrders();
    }, []);

    if (isLoading) {
        return (
            <div className="flex items-center justify-center p-12">
                <Loader2 className="h-8 w-8 animate-spin text-muted-foreground" />
            </div>
        );
    }

    return (
        <div className="space-y-6">
            <div>
                <h2 className="text-2xl font-bold">Order History</h2>
                <p className="text-muted-foreground">
                    {orders.length === 0
                        ? "You have 0 orders."
                        : `Viewing ${orders.length} past orders.`}
                </p>
            </div>
            <Separator />

            <div className="grid gap-4">
                {orders.length > 0 ? (
                    orders.map((order: any) => (
                        <Card key={order._id} className="group hover:border-primary/50 transition-colors">
                            <CardContent className="p-4 md:p-6">
                                <div className="flex flex-col md:flex-row md:items-center justify-between gap-4">
                                    <div className="space-y-1">
                                        <div className="flex items-center gap-3">
                                            <span className="font-mono font-bold text-sm uppercase">#{order._id.slice(-8)}</span>
                                            <Badge variant={order.status === "delivered" ? "default" : "secondary"}>
                                                {order.status}
                                            </Badge>
                                        </div>
                                        <div className="flex items-center gap-2 text-xs text-muted-foreground mt-1">
                                            <Clock className="h-3 w-3" />
                                            Ordered on {new Date(order.createdAt).toLocaleDateString()}
                                        </div>
                                    </div>

                                    <div className="flex items-center justify-between md:justify-end gap-8">
                                        <div className="text-right">
                                            <p className="text-xs text-muted-foreground uppercase tracking-wider font-bold">Total</p>
                                            <p className="text-lg font-bold">${order.total.toFixed(2)}</p>
                                        </div>
                                        <Link href={`/account/orders/${order._id}`}>
                                            <Button variant="ghost" size="icon" className="group-hover:translate-x-1 transition-transform">
                                                <ChevronRight className="h-5 w-5" />
                                            </Button>
                                        </Link>
                                    </div>
                                </div>
                            </CardContent>
                        </Card>
                    ))
                ) : (
                    <div className="text-center py-12 flex flex-col items-center gap-4">
                        <Package className="h-12 w-12 text-muted-foreground/30" />
                        <p className="text-muted-foreground">You haven't placed any orders yet.</p>
                        <Link href="/products">
                            <Button>Start Shopping</Button>
                        </Link>
                    </div>
                )}
            </div>
        </div>
    );
}
